$font-weights: ('regular': 400, 'medium': 500, 'semibold': 600, 'bold': 700, 'extrabold': 800);

@mixin font-set($name, $weight: regular) {
  @if $name == 'head-0' {
    font-weight: map-get($font-weights, 'extrabold');
    font-size: 36px;
    line-height: 46px;
  }

  @if $name == 'head-1' {
    font-weight: map-get($font-weights, 'extrabold');
    font-size: 26px;
    line-height: 36px;
  }

  @if $name == 'head-2' {
    font-weight: map-get($font-weights, 'extrabold');
    font-size: 22px;
    line-height: 32px;
  }

  @if $name == 'head-3' {
    font-weight: map-get($font-weights, 'bold');
    font-size: 18px;
    line-height: 28px;
  }

  @if $name == 'head-4' {
    font-weight: map-get($font-weights, 'extrabold');
    font-size: 16px;
    line-height: 24px;
  }

  @if $name == 'body-1' {
    font-weight: map-get($font-weights, $weight);
    font-size: 16px;
    line-height: 28px;
  }

  @if $name == 'body-2' {
    font-weight: map-get($font-weights, $weight);
    font-size: 14px;
    line-height: 20px;
  }

  @if $name == 'body-2-text' {
    font-weight: map-get($font-weights, $weight);
    font-size: 14px;
    line-height: 22px;
  }

  @if $name == 'body-3' {
    font-weight: map-get($font-weights, $weight);
    font-size: 12px;
    line-height: 16px;
  }

  @if $name == 'body-4' {
    font-weight: map-get($font-weights, $weight);
    font-size: 11px;
    line-height: 14px;
  }

  @if $name == 'body-5' {
    font-weight: map-get($font-weights, $weight);
    font-size: 10px;
    line-height: 14px;
  }

  @if $name == 'body-6' {
    font-weight: map-get($font-weights, $weight);
    font-size: 9px;
    line-height: 14px;
  }
}
