body {
  font-family: 'Squad Heavy', sans-serif;
  overflow-x: hidden;
  color: #000000;
  width: 100%;
  background: $white;
  font-style: normal;
  margin: 0;
}

.content-box {
  padding-right: 16px;
  padding-left: 16px;

  @include media-breakpoint-up(md) {
    max-width: 85%;
    margin: 0 auto;
  }

  @include media-breakpoint-up(lg) {
    max-width: 70%;
    margin: 0 auto;
  }
}

.container {
  max-width: 100%;

  @include media-breakpoint-up(md) {
    max-width: 85%;
  }

  &.only-desktop-view {
    display: none;

    @include media-breakpoint-up(lg) {
      display: block;
    }
  }
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}