@import 'buttons';

.product {
  &__main-area {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 100%;
    margin: auto 0;
    width: 100%;
    overflow-x: hidden;

    a {
      text-decoration: underline;
      font-weight: bold;
      cursor: pointer;
      color: #000000;
    }
  }

  &__presentation {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    overflow: hidden;

    @include media-breakpoint-up(md) {
      height: 598px;
    }

    &--image {
      position: absolute;
      width: 100%;
      height: 100%;
      background: url('../assets/images/main-photo.png') no-repeat bottom right;
      background-size: contain;
      z-index: 99;
      right: -47%;

      @include media-breakpoint-up(md) {
        right: 0;
        background-size: 40%;
      }
      @include media-breakpoint-up(xl) {
        background-size: auto;
      }
    }

    &--logo {
      position: absolute;
      width: 100%;
      height: 100%;
      background: url('../assets/images/otp-logo-bottom.png') no-repeat bottom left;
      z-index: 100;
      opacity: 0;

      @include media-breakpoint-up(md) {
        opacity: 1;
        background-size: 40%;
      }

      @include media-breakpoint-up(lg) {
        background-size: auto;
      }
    }

    &--info-block {
      display: flex;
      flex-direction: column;
      z-index: 100;
      padding-top: 48px;

      @include media-breakpoint-up(md) {
        padding-top: 58px;
      }
    }

    &--title {
      font-weight: 800;
      font-size: 40px;
      line-height: 48px;
      letter-spacing: -0.02em;

      @include media-breakpoint-up(md) {
        font-size: 72px;
        line-height: 88px;
      }
    }

    &--description {
      font-family: 'Squad Regular', sans-serif;
      font-size: 24px;
      width: 270px;
      line-height: 32px;
      padding: 24px 0 64px;

      @include media-breakpoint-up(md) {
        font-family: 'Squad Heavy', sans-serif;
        width: 100%;
        padding: 40px 0 32px;
      }
    }

    &--btn-container {
      padding-bottom: 45px;

      @include media-breakpoint-up(md) {
        padding-bottom: 0;
      }
    }
  }

  &__advantages {
    position: relative;
    height: 100%;

    @include media-breakpoint-up(md) {
      height: 715px;
    }

    &--business {
      background: url('../assets/images/black-otp-img.png') no-repeat;
      height: 466px;
      background-size: 100% 100%;

      @include media-breakpoint-up(lg) {
        height: 715px;
      }
    }

    &--client {
      background: url('../assets/images/white-otp-img.png') no-repeat;
      height: 466px;
      background-size: 100% 100%;

      @include media-breakpoint-up(lg) {
        height: 715px;

      }
    }

    &--title {
      font-weight: 800;
      font-size: 40px;
      line-height: 48px;
      letter-spacing: -0.4px;
      padding-top: 72px;
      width: auto;

      @include media-breakpoint-up(lg) {
        padding-top: 197px;
        width: max-content;
      }

      &.white-text {
        color: $white;
      }

      &--description-business {
        padding-top: 32px;
      }
    }

    &--description-business {
      padding-top: 32px;
      padding-bottom: 110px;

      @include media-breakpoint-up(md) {
        padding-top: 64px;
        padding-bottom: 0;
      }
    }

    ul {
      li {
        font-family: 'Squad Regular', sans-serif;
        padding-bottom: 40px;
        color: $white;

        &:last-child {
          padding-bottom: 0;
        }

        @include media-breakpoint-up(md) {
          font-family: 'Squad Heavy', sans-serif;
        }
      }
    }

    &--description-client {
      padding-top: 32px;
      width: 100%;
      padding-bottom: 104px;

      @include media-breakpoint-up(md) {
        padding-top: 43px;
        width: 350px;
        padding-bottom: 0;
      }

      ul {
        li {
          font-family: 'Source Sans Pro', sans-serif;
          font-weight: 600;
          padding-bottom: 40px;
          color: $black;

          &:before {
            content: '';
            width: 12px;
            height: 12px;
            margin-right: 12px;
            background: #428B26;
            border-radius: 50%;
            display: inline-flex;
          }

          &:last-child {
            padding-bottom: 0;
          }
        }
      }
    }
  }

  &__sale {
    height: 100%;
    padding-bottom: 72px;

    @include media-breakpoint-up(lg) {
      height: 659px;
    }

    &--number {
      font-weight: 800;
      font-size: 24px;
      line-height: 32px;
      color: #52AE32;
      display: flex;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      border: 1px solid #52AE32;
      box-sizing: border-box;
      align-items: center;
      justify-content: center;
    }

    &--title {
      padding-top: 80px;
      padding-bottom: 40px;
      font-family: 'Squad Heavy', sans-serif;
      font-style: normal;
      font-weight: 800;
      font-size: 32px;
      line-height: 40px;
      margin: 0;
      width: 100%;

      @include media-breakpoint-up(lg) {
        font-weight: 800;
        font-size: 40px;
        line-height: 48px;
        letter-spacing: -0.4px;
        padding-top: 109px;
        padding-bottom: 64px;
        width: 640px;
      }
    }

    &--box-container {
      display: flex;
      overflow-x: auto;
      overflow-y: hidden;
    }

    &--box {
      width: 224px;
      margin-right: 81px;
      position: relative;
      flex-grow: 1;
      -ms-flex-negative: 0;
      flex-shrink: 0;

      @include media-breakpoint-up(xl) {
        flex-shrink: 1;
      }

      &-title {
        padding-top: 24px;
        padding-bottom: 8px;
        font-size: 20px;
        font-weight: normal;
        line-height: 28px;

        @include media-breakpoint-up(lg) {
          padding-bottom: 16px;
          font-weight: 800;
          font-size: 24px;
          line-height: 32px;
        }
      }

      &-description {
        font-family: "Source Sans Pro", sans-serif;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;

        @include media-breakpoint-up(lg) {
          font-size: 18px;
          line-height: 32px;
        }
      }
    }
  }

  &__options {
    position: relative;
    display: flex;

    @include media-breakpoint-up(lg) {
      height: 100%;
    }

    &--image {
      position: absolute;
      width: 100%;
      height: 1115px;
      background: url('../assets/images/media-mobile.png') no-repeat right;
      background-size: contain;
      z-index: 99;

      @include media-breakpoint-up(sm) {
        height: 100%;
      }
      @include media-breakpoint-up(lg) {
        position: relative;
        height: 806px;
        background: url('../assets/images/media.png') no-repeat left;
        background-size: contain;
        width: 29%;
      }
    }

    &--container {
      width: 97%;
      margin-top: 55px;
      background: rgba(255, 255, 255, 0.8);
      padding-right: 16px;
      padding-left: 16px;
      z-index: 100;

      @include media-breakpoint-up(md) {
        max-width: 85%;
        padding: 0;
        margin: 0 auto;
      }

      @include media-breakpoint-up(lg) {
        width: 71%;
      }
    }

    &--title {
      width: 100%;
      font-weight: 800;
      font-size: 40px;
      line-height: 48px;
      letter-spacing: -0.4px;
      padding-bottom: 57px;
      padding-top: 32px;

      @include media-breakpoint-up(lg) {
        width: 800px;
        font-weight: 800;
        font-size: 72px;
        line-height: 88px;
        padding-bottom: 48px;
        padding-top: 85px;
      }
    }

    &--box-container {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;

      @include media-breakpoint-up(lg) {
        flex-direction: row;
      }
    }

    &--box {
      width: 256px;
      padding-bottom: 48px;
      border: 1px solid transparent;

      @include media-breakpoint-up(lg) {
        margin-right: 50px;
        padding-bottom: 80px;
      }

      &-title {
        padding-bottom: 16px;
        color: #1B1F27;
        font-weight: 800;
        font-size: 24px;
        line-height: 32px;
      }

      &-description {
        font-family: 'Source Sans Pro', sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        color: #494949;
      }
    }

    &--btn-container {
      padding-bottom: 32px;
    }
  }

  &__variant {
    margin-bottom: 68px;

    @include media-breakpoint-up(lg) {
      margin-top: 88px;
      margin-bottom: 198px;

    }

    &--box-container {
      display: flex;
      flex-direction: column;
    }

    &--image, &--slider-image {
      width: 100%;
      height: 100%;
      min-height: 622px;
      position: relative;
      margin: 0 auto;
      background-position: center;

      @include media-breakpoint-up(lg) {
        width: 86%;
        height: 100%;
        min-height: 830px;
        position: relative;
        margin: 0 auto;
      }
      &.variantOne {
        background: url('../assets/images/mobile.png') no-repeat center;
        background-size: contain;
      }
      &.variantTwo {
        background: url('../assets/images/mobile2.png') no-repeat center;
        background-size: contain;
      }
      &.variantThree {
        background: url('../assets/images/mobile3.png') no-repeat center;
        background-size: contain;
      }
    }

    &--box {
      padding: 23px 0 22px;
      display: flex;
      font-family: 'Source Sans Pro', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 130%;
      color: #000000;
      box-sizing: border-box;
      text-align: left;

      &:last-child {
        margin-bottom: 32px;

        @include media-breakpoint-up(lg) {
          margin-bottom: 0;
        }
      }

      @include media-breakpoint-up(lg) {
        cursor: pointer;
        font-size: 20px;
        line-height: 130.8%;
        color: #000000;
        width: 483px;
        margin-bottom: 25px;
      }

      .variant-icon {
        font-family: 'Squad Heavy', sans-serif;
        font-weight: 800;
        font-size: 40px;
        letter-spacing: -0.4px;
        color: #9BD46A;
        float: left;
        padding: 5px 22px 29px 26px;

        @include media-breakpoint-up(lg) {
          padding: 0 16px 29px 26px;
        }
      }

      .variant-text {
        margin-right: 20px;
        @include media-breakpoint-up(lg) {
          width: 425px;
          margin-right: auto;
        }
      }

      &.selected {
        border: 0;

        @include media-breakpoint-up(lg) {
          border: 1px solid #52AE30;
          border-radius: 4px;
        }
      }
    }

    &--title {
      font-weight: 800;
      font-size: 32px;
      line-height: 40px;
      width: 100%;
      margin-top: 80px;

      @include media-breakpoint-up(lg) {
        font-weight: 800;
        font-size: 72px;
        line-height: 88px;
        width: 483px;
        margin-top: 0;
      }
    }

    &--btn-container {
      margin-top: 77px;
    }

    .swiper-container {
      display: block;

      @include media-breakpoint-up(lg) {
        display: none;
      }
    }
  }

  &__connection {
    background: #FAF8EE;

    &--title {
      font-weight: 800;
      font-size: 40px;
      line-height: 48px;
      letter-spacing: -0.4px;
      margin-top: 68px;
      margin-bottom: 40px;

      @include media-breakpoint-up(lg) {
        margin-top: 112px;
        margin-bottom: 56px;
        font-weight: 800;
        font-size: 72px;
        line-height: 88px;
      }
    }

    &--box-container {
      display: flex;
      flex-direction: column;

      @include media-breakpoint-up(lg) {
        flex-direction: row;
      }
    }

    &--number {
      display: none;

      @include media-breakpoint-up(lg) {
        font-weight: 800;
        font-size: 24px;
        line-height: 32px;
        color: #A1AEB7;
        display: flex;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        border: 1px solid #A1AEB7;
        box-sizing: border-box;
        align-items: center;
        justify-content: center;
      }
    }

    &--box {
      margin-right: 81px;
      position: relative;

      @include media-breakpoint-up(lg) {
        width: 100%;
      }

      &-title {
        padding-top: 24px;
        padding-bottom: 8px;
        font-size: 20px;
        font-weight: normal;
        line-height: 28px;
        display: none;

        @include media-breakpoint-up(lg) {
          padding-bottom: 16px;
          font-weight: 800;
          font-size: 24px;
          line-height: 32px;
          display: block;
        }
      }

      &-description {
        font-family: "Source Sans Pro", sans-serif;
        margin-bottom: 32px;
        display: flex;

        .connection-text {
          font-weight: normal;
          font-size: 16px;
          line-height: 24px;

          @include media-breakpoint-up(lg) {
            font-weight: normal;
            font-size: 18px;
            line-height: 32px;
            color: #000000;
          }
        }

        .connection-icon {
          font-family: 'Squad Heavy', sans-serif;
          font-weight: 800;
          font-size: 40px;
          letter-spacing: -0.4px;
          color: #1B1F27;
          float: left;
          display: block;
          padding: 0 22px 29px 0;

          @include media-breakpoint-up(lg) {
            display: none;
            padding: 0 22px 29px 26px;
          }
        }
      }
    }
    &--btn-container {
      margin-top: 32px;
      padding-bottom: 73px;
      @include media-breakpoint-up(lg) {
        margin-top: 80px;
        padding-bottom: 127px;
      }
    }
  }

  &__questions {
    padding-top: 48px;
    min-height: 823px;


    @include media-breakpoint-up(lg) {
      padding-top: 132px;
      min-height: 823px;
      height: 100%;
    }

    &--title {
      font-size: 40px;
      width: 173px;
      line-height: 48px;
      letter-spacing: -0.4px;
    }

    &--question {
      position: relative;
      border-bottom: 1px solid #F1F1F1;
      cursor: pointer;

      &:first-child {
        @include media-breakpoint-up(lg) {
          margin-top: -15px;
        }
      }

      &.expanded {
        .product__questions--question-description {
          display: block;
        }
        .question-icon {
          background: url('../assets/images/down.svg') no-repeat;
        }
      }

    .question-title-wrapper {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }

      .question-icon {
        background: url('../assets/images/up.svg') no-repeat;
        width: 25px;
        height: 10px;
        margin-left: 15px;
      }

      &-title {
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        padding-top: 24px;
        padding-bottom: 24px;
        width: 100%;

        @include media-breakpoint-up(lg) {
          font-weight: bold;
          font-size: 20px;
          line-height: 28px;
          color: #1B1F27;
        }
      }
      &-description {
        font-family: 'Source Sans Pro', sans-serif;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        padding-bottom: 24px;
        display: none;

        @include media-breakpoint-up(lg) {
          padding-top: 0;
        }
      }
    }
  }

  &__info {
    background: #EFF2F9;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 40px 16px 49px 16px;

    @include media-breakpoint-up(lg) {
      height: 209px;
      padding: 0;
    }

    &--text {
      font-family: 'Source Sans Pro', sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
    }
  }
}