.product-btn {
  font-family: 'Source Sans Pro', sans-serif;
  border-radius: 32px;
  padding: 13px 32px;
  height: 56px;
  border: 0;
  width: 100%;
  cursor: pointer;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  outline: none;

  @include media-breakpoint-up(md) {
    width: max-content;
    font-size: 20px;
    line-height: 28px;
  }

  &--orange {
    background: #FF6600;
    color: $white;

    &:hover, &:focus {
      background: #FF802B;
    }
  }

  &--green {
    background: #52AE30;
    color: $white;
    box-shadow: 0 15px 20px rgba(0, 0, 0, 0.1);

    &:hover, &:focus {
      background: #6ECD4A;
    }
  }
}